<template>
  <form class="form-inline higherForm">
    <div class="row">
      <div class="col-md-4 ta-c">
        <div class="form-group">
          <label for="projectName"></label>
          <input type="text" class="form-control" id="projectName" placeholder="" v-model="sea.requester">
        </div>
      </div>
      <div class="col-md-4 ta-c">
        <div class="form-group">
          <label for="agency"></label>
          <input type="text" class="form-control" id="agency" placeholder="" v-model="sea.requesterName">
        </div>
      </div>
      <div class="col-md-4 ta-c">
        <div class="form-group">
          <label for="tenderPerson"></label>
          <input type="text" class="form-control" id="tenderPerson" placeholder="" v-model="sea.applicantName">
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col-md-4 ta-c">
        <div class="form-group">
          <label for="projectName"></label>
          <div class="input-group">
            <select class="form-control" v-model="sea.amount" @change="search()">
              <option :value="AMO" v-for="(AMO, index) in amounts" :key="index">{{AMO}}</option>
            </select>
            <div class="input-group-addon"></div>
          </div>
        </div>
      </div>
      <div class="col-md-4 ta-c">
        <div class="form-group">
          <label for="agency"></label>
          <select class="form-control type" v-model="sea.type" @change="search()">
            <option :value="TYPE" v-for="(TYPE, index) in types" :key="index">{{TYPE}}</option>
          </select>
        </div>
      </div>
      <div class="col-md-4 ta-c"></div>
    </div>
    <div class="row">
      <div class="col-md-4 ta-c">
        <div class="form-group">
          <label for="tenderPerson"></label>
          <input type="date"
                 class="form-control"
                 placeholder=""
                 v-model.trim="sea.time.start"
                 @change="search()">
        </div>
      </div>
      <div class="col-md-4 ta-c">
        <div class="form-group">
          <label for="tenderPerson"></label>
          <input type="date"
                 class="form-control"
                 placeholder=""
                 v-model.trim="sea.time.end"
                 @change="search()">
        </div>
      </div>
      <div class="col-md-4">

      </div>
    </div>
    <div class="search-btns ta-c">
      <button type="button" class="btn my-btn submit-btn" @click="higherSearchEvent()"></button>
      <button type="button" class="btn my-btn draft-btn" @click="reset()"></button>
    </div>
  </form>
</template>

<script>
export default {
  name: 'businessCompleteSearchBar',
  data() {
    return {
      sea: {
        requester: '',
        requesterName: '',
        applicantName: '',
        time: {
          start: '',
          end: ''
        },
        amount: '',
        type: '',
      },
      amounts: [
        '',
        '0.1-2',
        '2-8',
        '8-20',
        '20'
      ],
      types: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ]
    };
  },
  methods: {
    search() {
      this.$emit('search', this.sea);
    }
  }
};
</script>

<style lang="sass" scoped>
.normal-wrap {
  margin-top: 100px;
}
.higherForm {
  margin-top: 90px;
  .form-group {
    width: 100%;
    label {
      width: 25%;
    }
    input {
      width: 70%;
    }
    .input-group {
      width: 70%;
    }
  }
}
.form-inline .type {
    width: 70%;
}
.row {
    margin-bottom: 20px;
}
.submit-btn {
  margin-right: 30px;
}
</style>



