<template>
  <div>
    <progress-bar :progress="progress"></progress-bar>
    <tab-control :initParts="parts"
                 :initBusiness="business"
                 :user="user"
                 @pathsChan="pathsChan"></tab-control>
  </div>
</template>

<script>
import progressBar from './progressBarSVG.vue';
import tabControl from './tabControl.vue';

export default {
  name: 'business',
  data() {
    return {
      parts: [],
      business: this.initBusiness
    };
  },
  props: ['initBusiness', 'user', 'progress'],
  mounted() {
    if (this.user.department) {
      if (this.user.department === '') {
        this.parts.splice(0, 0,
          {name: '', path: 'business-profile', state: true},
          {name: '', path: 'business-conduct', state: false},
          {name: '', path: 'billing-infor', state: false},
          {name: '', path: 'operat-history', state: false}
        );
      } else if (this.user.department === '') {
        this.parts.splice(0, 0,
          {name: '', path: 'business-profile', state: true}
        );
      } else if (this.user.department === '') {
        this.parts.splice(0, 0,
          {name: '', path: 'business-profile', state: true},
          {name: '', path: 'business-conduct', state: false},
          {name: '', path: 'billing-infor', state: false},
          {name: '', path: 'operat-history', state: false}
        );
      } else if (this.user.department === '') {
        this.parts.splice(0, 0,
          {name: '', path: 'business-profile', state: true},
          {name: '', path: 'operat-history', state: false}
        );
      } else if (this.user.department === '') {
        this.parts.splice(0, 0,
          {name: '', path: 'business-profile', state: true}
        );
      } else if (this.user.department === '') {
        this.parts.splice(0, 0,
          {name: '', path: 'business-profile', state: true},
          {name: '', path: 'billing-infor', state: false}
        );
      } else if (this.user.department === '') {
        this.parts.splice(0, 0);
      }
    }
  },
  methods: {
    pathsChan(paths) {
      this.$emit('pathsChan', paths);
    }
  },
  components: {
    progressBar,
    tabControl
  }
};
</script>

<style lang="sass" scoped>

</style>



